import React, { Component } from "react";
import { Link, Redirect } from "react-router-dom";
import { connect } from "react-redux";
import PropTypes from "prop-types";
import { login } from "../../actions/auth";
import { Form, Input, Button, Checkbox, Alert } from "antd";
import { updateTitle } from "../common/UpdateTitle";

class Login extends Component {
  state = {
    has_login_error: false,
    login_error_msg: "",
  };
  componentDidMount() {
    updateTitle({
      title: "Login",
    });
  }
  componentDidUpdate(oldProps) {
    const { error } = this.props;

    if (error !== oldProps.error) {
      console.log(error);

      if (error.msg.detail) {
        this.setState({
          ...this.state,
          has_login_error: true,
          login_error_msg: error.msg.detail,
        });
      }
      if (error.msg.username) {
        this.setState({
          has_login_error: true,
          login_error_msg: error.msg.username.join(),
        });
      }
    }
  }

  static propTypes = {
    login: PropTypes.func.isRequired,
    isAuthenticated: PropTypes.bool,
  };

  onSubmit = (e) => {
    this.setState({
      has_login_error: false,
      login_error_msg: "",
    });
    this.props.login(e.username, e.password);
  };
  render() {
    const { from } = this.props.location.state || { from: { pathname: "/" } };
    if (this.props.isAuthenticated) {
      return <Redirect to={from} />;
    }
    let { login_error_msg, has_login_error } = this.state;

    return (
      <div className="mdk-header-layout__content page-content ">
        <div className="pt-32pt pt-sm-64pt pb-32pt">
          <div className="container page__container page-section pb-0">
            <h1 className="h2 text-center">Login</h1>
          </div>
          <div className="container page__container">
            <Form
              className="col-md-5 p-0 mx-auto"
              name="basic"
              initialValues={{
                remember: true,
              }}
              onFinish={this.onSubmit}
            >
              {has_login_error ? (
                <Alert
                  className="my-3"
                  message={login_error_msg}
                  type="error"
                  showIcon
                  closable
                />
              ) : null}

              <Form.Item
                label="Username"
                name="username"
                className="form-group"
                rules={[
                  {
                    required: true,
                    message: "Please input your username!",
                  },
                ]}
              >
                <Input className="form-control" />
              </Form.Item>

              <Form.Item
                label="Password"
                name="password"
                className="form-group"
                rules={[
                  {
                    required: true,
                    message: "Please input your password!",
                  },
                ]}
              >
                <Input.Password autoComplete="on" />
              </Form.Item>

              <Form.Item>
                <Form.Item name="remember" valuePropName="checked" noStyle>
                  <Checkbox>Remember me</Checkbox>
                </Form.Item>

                <Link className="login-form-forgot float-right" to="#">
                  Forgot password?
                </Link>
              </Form.Item>

              <Form.Item>
                <Button
                  type="primary"
                  htmlType="submit"
                  className="login-form-button btn btn-primary"
                >
                  Login
                </Button>
                <Link className="float-right" to="/register">
                  Register Now!
                </Link>
              </Form.Item>
            </Form>
          </div>
        </div>
        <div className="bg-body pb-32pt pb-md-64pt text-center"></div>
      </div>
    );
  }
}

const mapStateToProps = (state) => ({
  isAuthenticated: state.auth.isAuthenticated,
  error: state.errors,
});

export default connect(mapStateToProps, { login })(Login);
